import { autoserialize as a } from 'cerialize';

/**
 *  represents the limit amount for each of the 11 TCC's and whether the amounts are different than default limits.
 * @see https://dtl.fusion.proxy.mastercard.int/confluence/pages/viewpage.action?pageId=221124414
 */
export class TccLimitAmount {

  /** TCC's for each account range brand + product code + card type + card present indicator. */
  @a tcc: string;

  /**
   * Each account range brand + product code + card type + card present indicator + tcc
   * should have a limit amount associated.
  */
  @a amount: number;

  /** Indicator whether the limit is different than default (higher , lower or equal). */
  @a overridden: string;

  constructor(initial?: TccLimitAmount) {
    Object.assign(this, initial);
  }

}
