import { deserialize as d, inheritSerialization } from 'cerialize';
import { BlockType } from './block-type';
import { StandInBlock } from './standin-block';

/**
 * Represents a generic view of a Range Block in the system.
 * @see https://dtl.fusion.proxy.mastercard.int/confluence/pages/tinyurl.action?urlIdentifier=lIPyD
 */
@inheritSerialization(StandInBlock)
export class RangeBlock extends StandInBlock {

  /** The starting account range number for this Range Block (omit for ICA Block). */
  @d startingBlockRange: string;

  /** The ending account range number for this Range Block (omit for ICA Block). */
  @d endingBlockRange: string;

  constructor(initial?: RangeBlock) {
    super(initial);
    Object.assign(this, initial);
    this.blockType = BlockType.RANGE;
  }

}
