import { autoserialize as a, deserialize as d } from 'cerialize';

/**
 * Represents a view of a Range Block Criteria resource in the system.
 * @see https://dtl.fusion.proxy.mastercard.int/confluence/pages/tinyurl.action?urlIdentifier=lIPyD
 */
export class BlockCriteria {

  /** The unique id associated to this Criteria (not intended for display). */
  @d criteriaId: number;

  /** The unique id of the associated BOM element for this Criteria. */
  @a elementId: number;

  /** The valid value identifier or actual value for this Criteria. */
  @a value: string | null;

  constructor(initial?: BlockCriteria) {
    Object.assign(this, initial);
  }

}
