import { autoserialize as a, autoserializeAs as aas} from 'cerialize';
import { TccLimitAmount } from './tcc-limit-amount';

/**
 * Represents a generic view of a Range Block or ICA Block in the system.
 * @see https://dtl.fusion.proxy.mastercard.int/confluence/pages/tinyurl.action?urlIdentifier=lIPyD
 */
export class TccLimit {

  public static OnSerialized(_: TccLimit, json: any): void {
    json['brandProductCode'] = json['brandProductCode'].split('-')[0];
  }

  public static OnDeSerialized(_: TccLimit, json: any): void {
    json.tccLimitAmount.forEach((item: TccLimitAmount) => {
      json.tccLimitAmount['tcc' + item.tcc] = item.amount;
      json.tccLimitAmount['overridden' + item.tcc] = item.overridden;
    });
  }
  /**
   * The unique id associated with an overridden limit.
   * There would be no limit id for a default limit, since its set at tcc level in a static UDT)
  */
  @a tccLimitId: number;

  /**
   * The unique id associated with an overridden limit.
   * there would be no limit id for a default limit, since its set at tcc level in a static UDT
  */
  @a memberId: number;

  /** The starting account range number for this tcc limit. */
  @a startingAccntRange: string;

  /** The ending account range number for this  tcc limit. */
  @a endingAccntRange: string;

  /** Brand product code for that account range */
  @a brandProductCode: string;

  /** Indicates if this is a VIP */
  @a vipIndicator: boolean;

  /** Card type for that account range */
  @a cardType: string;

  /** An array of BlockCriteria resources associated to this block. */
  @aas(TccLimitAmount) tccLimitAmount: TccLimitAmount[] | null;

  error?: any;
  tccA: number;
  tccC: number;
  tccF: number;
  tccH: number;
  tccO: number;
  tccP: number;
  tccR: number;
  tccT: number;
  tccU: number;
  tccX: number;
  tccZ: number;

  /** The datetime that the range block was last updated (same as crteDttm initially). */
  @aas(Date) lastUpdateDttm: Date;

  /** The userId that last updated the range block (same as createUser initially). */
  @a lastUpdateUsr: string;

  constructor(initial?: TccLimit) {
    Object.assign(this, initial);
  }

}
