import { autoserializeAs as aas, deserialize as d, deserializeAs as das} from 'cerialize';
import { BlockCriteria } from './block-criteria';
import { BlockType } from './block-type';

/**
 * Represents a generic view of a Range Block or ICA Block in the system.
 * @see https://dtl.fusion.proxy.mastercard.int/confluence/pages/tinyurl.action?urlIdentifier=lIPyD
 */
export class StandInBlock {

  /** The unique id associated to this block item. May be rangeBlockId or icaGroupId. */
  @d blockId: number;

  /** Either 'RANGE' or 'ICA' indicating block type. */
  @das(BlockType) blockType: BlockType;

  /** The ICA unique id for the MPS account range that this Range Block falls within. */
  @d memberId: number;

  /** An array of BlockCriteria resources associated to this block. */
  @aas(BlockCriteria) blockCriteria: BlockCriteria[] = [];

  /** The datetime that the range block was last updated (same as crteDttm initially). */
  @das(Date) lastUpdateDttm: Date;

  /** The userId that last updated the range block (same as createUser initially). */
  @d lastUpdateUsr: string;

  /** The datetime that the range block was created. */
  @das(Date) crteDttm: Date;

  /** The userId that created the range block. */
  @d crteUsr: string;

  constructor(initial?: StandInBlock) {
    Object.assign(this, initial);
  }

  getBlockCriteria(id: number) {
    const found = this.blockCriteriaÂ && this.blockCriteria.find((item: BlockCriteria) => item.elementId === id );
    if (found) {
      return found;
    }
    const blockCriteria: BlockCriteria = new BlockCriteria();
    blockCriteria.elementId = id;
    blockCriteria.value = null;
    return blockCriteria;
  }

}
