import { autoserialize as a, autoserializeAs as aas} from 'cerialize';

export class DictionaryEntry {
  @a code: string;
  @a description: string;
}

export class Dictionary {
  public static OnDeserialized(instance: Dictionary, _: any): void {
    if (!instance.value) {
      instance.parsed = {};
      return;
    }
    instance.parsed = instance.value.reduce(
      (map, entry) => {
        map[entry.code] = entry.description;
        return map;
      },
      {}
    );
  }

  @aas(DictionaryEntry) value: DictionaryEntry[];
  parsed: { [key: string]: string };
}
