import { autoserialize as a, autoserializeAs as aas } from 'cerialize';
import * as momentNS from 'moment';

const moment = momentNS;

export class Filters {
    @a mcc?: string;
    @aas('start-range') private startRange?: string;
    @aas('end-range') private endRange?: string;
    @aas('block-type') private blockType?: string;
    @aas('member-id') private memberId?: string;
    @aas('pos-ctry') private posCtry?: string;
    @aas(Date, 'create-dttm-start') private createDttmStart?: Date;
    @aas(Date, 'create-dttm-end') private createDttmEnd?: any;
    @aas('tx-type') private txType?: string;
    @aas('pan-entry') private panEntry?: string;
    @aas('cat-level') private catLevel?: string;
    @aas('acq-id') private acqId?: string;

    constructor(initial?: Filters) {
      Object.assign(this, initial);
    }

    get 'start-range'() { return this.startRange; }
    set 'start-range'(v) { this.startRange = v; }

    get 'end-range'() { return this.endRange; }
    set 'end-range'(v) { this.endRange = v; }

    get 'block-type'() { return this.blockType; }
    set 'block-type'(v) { this.blockType = v; }

    get 'member-id'() { return this.memberId; }
    set 'member-id'(v) { this.memberId = v; }

    get 'pos-ctry'() { return this.posCtry; }
    set 'pos-ctry'(v) { this.posCtry = v; }

    get 'create-dttm-start'() { return this.createDttmStart; }
    set 'create-dttm-start'(v) { this.createDttmStart = v; }

    get 'create-dttm-end'() {
      if (this['create-dttm-start']) {
        this.createDttmEnd = moment(this['create-dttm-start']).add(1, 'days');
      } else {
        this.createDttmEnd = this['create-dttm-start'];
      }
      return this.createDttmEnd;
    }
    set 'create-dttm-end'(_) { /* noop */ }

    get 'tx-type'() { return this.txType; }
    set 'tx-type'(v) { this.txType = v; }

    get 'pan-entry'() { return this.panEntry; }
    set 'pan-entry'(v) { this.panEntry = v; }

    get 'cat-level'() { return this.catLevel; }
    set 'cat-level'(v) { this.catLevel = v; }

    get 'acq-id'() { return this.acqId; }
    set 'acq-id'(v) { this.acqId = v; }

}
